
import ViewBox from 'vux/src/components/view-box/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'

import { WHeader } from '@/components/wx'

import IconWchat from './icons/wchat'
import IconContact from './icons/contact'
import IconExplore from './icons/explore'
import IconProfile from './icons/profile'

export default {
name: 'wchat',
computed: {
title () {
return this.$route.meta.title
},
pageIndex () {
return this.$route.meta.tabIndex
}
},
components: {
ViewBox,
WHeader,
Tabbar,
TabbarItem,

IconWchat,
IconContact,
IconExplore,
IconProfile
}
}



