
import Search from 'vux/src/components/search/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import { mapState } from 'vuex'
import HeaderRight from './header-right'
import { format } from '../../utils'

export default {
name: 'message',
data () {
return {
head_icon: 'http://iph.href.lu/200x200?text=%E9%BB%98%E8%AE%A4%E5%A4%B4%E5%83%8F'
}
},
computed: mapState(['sessionList']),
filters: {
lastMessage (session) {
const len = session.messages.length
return session.messages[len - 1]
},
messageText: message => message.content.text,
messageTime (message) {
const date = new Date(message.date)
const now = new Date()
const diff = now - date
const day = 24 * 60 * 60 * 1000
let time
if (diff < day && date.getDate() === now.getDate()) { // 
time = format(date, 'hh:mm')
} else if (diff > day && diff < 2 * day && date.getDate() === now.getDate() + 1) {  // 
time = ''
} else if (diff > 2 * day) {
time = format(date, 'MMdd')
}
return time
}
},
components: {
Group,
Search,
Cell
},
HeaderRight
}



