import LoadingPlugin from 'vux/src/plugins/loading/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'


const plugin = {
install (Vue) {

Vue.use(LoadingPlugin)
Vue.use(ToastPlugin)

Vue.prototype.$toast = Vue.$toast = options => {
if (!options.text) {
options = {
type: 'text',
text: options,
position: 'bottom'
}
}
Vue.$vux.toast.show(options)
}

Vue.prototype.$loading = Vue.$loading = options => {
if (!options || !options.text) {
console.log(options)
options = { text: options || '...' }
}
Vue.$vux.loading.show(options)
}

Vue.prototype.$hide = Vue.$hide = options => {
Vue.$vux.toast.hide()
Vue.$vux.loading.hide()
}
}
}

export default plugin



