import WChat from '@/pages/wchat'
import Explore from '@/pages/explore'
import Profile from '@/pages/profile'
import Contact from '@/pages/contact'
import Message from '@/pages/message'

export default {
  path: '/wchat',
  name: 'wchat',
  component: WChat,
  children: [
    {
      path: 'message',
      name: 'message',
      components: {
        default: Message,
        'header-right': Message.HeaderRight
      },
      meta: {
        title: '',
        tabIndex: 0
      }
    },
    {
      path: 'contact',
      name: 'contact',
      components: {
        default: Contact,
        'header-right': Contact.HeaderRight
      },
      meta: {
        title: '',
        tabIndex: 1
      }
    },
    {
      path: 'explore',
      name: 'explore',
      component: Explore,
      meta: {
        title: '',
        tabIndex: 2
      }
    },
    {
      path: 'profile',
      name: 'profile',
      component: Profile,
      meta: {
        title: '',
        tabIndex: 3
      }
    }
  ]
}



