
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import { mapGetters } from 'vuex'

export default {
name: 'profile',
computed: {
...mapGetters('profile', ['name', 'username', 'headIcon'])
},
filters: {
prefix (username) {
return username && `${username}`
}
},
components: {
Group,
Cell
}
}



