
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

import ChatDialog from './chat-dialog'
import ChatBar from './chat-bar'
import { mapState } from 'vuex'

export default {
computed: {
...mapState(['sessionList']),
session () {
const { index, username } = this.$route.query
if (username) {
return {
name: username,
members: [username],
messages: []
}
}
return this.sessionList[index]
}
},
methods: {
send (text) {

const { id, name, members } = this.session
const username = sessionStorage.getItem('username')
const data = {
session: { id, name, members },
message: {
from: { username },
content: { text },
date: Date.now()
}
}

this.$send(JSON.stringify(data))

this.$store.commit('addMessage', data)
}
},
directives: {

scroll: {
bind (el) {
setTimeout(() => {
el.parentNode.scrollTop = el.scrollHeight
}, 1)
},
update (el) {
setTimeout(() => {
el.parentNode.scrollTop = el.scrollHeight
}, 1)
}
}
},
components: {
ViewBox,
XHeader,
ChatDialog,
ChatBar
}
}



